@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  margin: 0;
  padding: 0;
  font-family:"Courier New", Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left, rgb(115, 121, 201), rgb(219, 129, 147));
  background: linear-gradient(to right, rgb(115, 121, 201), rgb(219, 129, 147));
  font-family: 'Roboto', sans-serif;
}

a, a:focus, button, button:focus {
  cursor: pointer;
  outline:none;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.loaderBig {
  color: #ffffff;
  font-size: 20px;
  margin: 100px auto;
  width: 1em;
  height: 1em;
  border-radius: 50%;
  position: relative;
  text-indent: -9999em;
  -webkit-animation: load4 1.3s infinite linear;
  animation: load4 1.3s infinite linear;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  z-index: 2;
}
@-webkit-keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}
@keyframes load4 {
  0%,
  100% {
    box-shadow: 0 -3em 0 0.2em, 2em -2em 0 0em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 0;
  }
  12.5% {
    box-shadow: 0 -3em 0 0, 2em -2em 0 0.2em, 3em 0 0 0, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  25% {
    box-shadow: 0 -3em 0 -0.5em, 2em -2em 0 0, 3em 0 0 0.2em, 2em 2em 0 0, 0 3em 0 -1em, -2em 2em 0 -1em, -3em 0 0 -1em, -2em -2em 0 -1em;
  }
  37.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 0, 2em 2em 0 0.2em, 0 3em 0 0em, -2em 2em 0 -1em, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  50% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 0em, 0 3em 0 0.2em, -2em 2em 0 0, -3em 0em 0 -1em, -2em -2em 0 -1em;
  }
  62.5% {
    box-shadow: 0 -3em 0 -1em, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 0, -2em 2em 0 0.2em, -3em 0 0 0, -2em -2em 0 -1em;
  }
  75% {
    box-shadow: 0em -3em 0 -1em, 2em -2em 0 -1em, 3em 0em 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0.2em, -2em -2em 0 0;
  }
  87.5% {
    box-shadow: 0em -3em 0 0, 2em -2em 0 -1em, 3em 0 0 -1em, 2em 2em 0 -1em, 0 3em 0 -1em, -2em 2em 0 0, -3em 0em 0 0, -2em -2em 0 0.2em;
  }
}


.loaderSmall {
  display: inline-block;
  color: #ffffff;
  font-size: 3px;
  width: 1em;
  height: 1em;
  top:50%;
  right:-50%;
  border-radius: 50%;
  position: absolute;
  text-indent: -9999em;
  -webkit-animation: load4 1.3s infinite linear;
  animation: load4 1.3s infinite linear;
  -webkit-transform: translateZ(0);
  transform: translateZ(0) translate(0,-50%);
}



.App {
  text-align: center;
}
.particles {
  position: fixed;
  top:0;
  left:0;
  bottom:0;
  right:0;
  z-index: -1;
}
* { 
    box-sizing: border-box; 
  }
  .ReactModal__Overlay{
    
    position:fixed;
    top                   : 0;
    left                  : 0;
    right:0;
    bottom:0;
    background-color: rgba(255, 255, 255, 0.75);
  }
  .ReactModal__Content {
    position:absolute;
    top                   : 50%;
    left                  : 50%;
    right:auto;
    bottom:auto;
    -webkit-transform             : translate(-50%, -50%);
            transform             : translate(-50%, -50%);
    background:white;
    padding: 10px;
    max-height:80vh;
    max-width:90vw;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    outline:none;
    border-radius:5px;
    border: 1px solid #ccc,
  }
  table {
   
    background:#fff;
   border-radius: 5px;
    margin:10px auto;
    box-shadow:(0px 0px 5px rgba(0,0,0,0.3));
    text-align: center;
  }
  
  thead {
    border-radius:5px;
  }

  thead th {
    
    font-size:16px;
    font-weight:400;
    color:#fff;
    text-shadow:(1px 1px 0px rgba(0,0,0,0.5));
    text-align:center;
    padding:10px;
    background-image:-webkit-linear-gradient(#646f7f, #4a5564);
    background-image:linear-gradient(#646f7f, #4a5564);
    border-top:1px solid #858d99;
    }
    thead th:first-child {
    border-top-left-radius:5px; 
    }
  
    thead th:last-child {
    border-top-right-radius:5px; 
    }
  tbody {
    -webkit-animation:show-up 1.5s 1;
            animation:show-up 1.5s 1;
  }
  tbody tr td {
    font-family: 'Open Sans', sans-serif;
    font-weight:400;
    color:#5f6062;
    font-size:13px;
    padding:20px;
    border-bottom:1px solid #e0e0e0;
    
  }
  tbody tr:first-child td:first-child{
    color:gold;
    font-weight: bold;
  }
  tbody tr:nth-child(2) td:first-child{
    color:silver;
    font-weight: bold;
  }
  tbody tr:nth-child(3) td:first-child{
    color:brown;
    font-weight: bold;
  }
  
  tbody tr:nth-child(2n) {
    background:#f0f0f0;
  }
  
  tbody tr:last-child td {
    border-bottom:none;
  }
  tbody tr:first-child {
     border-bottom-left-radius:5px;
    }
    tbody tr:last-child {
    border-bottom-right-radius:5px;
    }
@-webkit-keyframes scale-up {
  0% {-webkit-transform: scale(0);transform: scale(0)}
  50% {-webkit-transform: scale(1.08);transform: scale(1.08)  }
  100% {-webkit-transform: scale(1);transform: scale(1) }
}
@keyframes scale-up {
  0% {-webkit-transform: scale(0);transform: scale(0)}
  50% {-webkit-transform: scale(1.08);transform: scale(1.08)  }
  100% {-webkit-transform: scale(1);transform: scale(1) }
}
@-webkit-keyframes show-up {
  0%{-webkit-transform: translate(0px, -200%);transform: translate(0px, -200%);}
  100% {-webkit-transform: translate(0px, 0px);transform: translate(0px, 0px);}
}
@keyframes show-up {
  0%{-webkit-transform: translate(0px, -200%);transform: translate(0px, -200%);}
  100% {-webkit-transform: translate(0px, 0px);transform: translate(0px, 0px);}
}

@media (max-width:500px) {
  .ReactModal__Content {
    padding:10px 0;
  }
  thead th {
    font-size: 13px;
  }
  tbody tr td {
    padding:10px 5px;
  }
}
nav.navigation {
    display:flex;
    justify-content:flex-end;
    align-items: center;
}
@media (max-width:500px) {
    nav.navigation{

    text-align: center;
}
    .navigation p {
        padding:0 0.5em 0 1em;
        text-align: center;
        margin: 1em 0.5em;
    }
}
.Tilt {
    display:inline-block;
}
.Tilt-inner {
    background-image:
    -webkit-linear-gradient(325deg, rgba(41, 181, 206, 0.3) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .3) 71%, rgba(31, 81, 167, 0.3) 95%),
    -webkit-linear-gradient(178deg, rgba(206, 190, 41, .8) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .7) 71%, rgba(137, 180, 255, .7) 95%),
    -webkit-linear-gradient(67deg, rgb(78, 72, 12) 0%, rgba(155, 31, 80, .6) 33%, rgb(7, 21, 32) 71%, rgb(29, 46, 77) 95%);
    background-image:
    linear-gradient(125deg, rgba(41, 181, 206, 0.3) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .3) 71%, rgba(31, 81, 167, 0.3) 95%),
    linear-gradient(-88deg, rgba(206, 190, 41, .8) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .7) 71%, rgba(137, 180, 255, .7) 95%),
    linear-gradient(23deg, rgb(78, 72, 12) 0%, rgba(155, 31, 80, .6) 33%, rgb(7, 21, 32) 71%, rgb(29, 46, 77) 95%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;


}
.imageForm {
    background-image:
    -webkit-linear-gradient(325deg, rgba(206, 190, 41, .3) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .3) 71%, rgba(137, 180, 255, .3) 95%),
    -webkit-linear-gradient(178deg, rgba(206, 190, 41, .8) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .7) 71%, rgba(137, 180, 255, .7) 95%),
    -webkit-linear-gradient(67deg, rgba(206, 190, 41, 1) 0%, rgba(155, 31, 80, .6) 33%, rgba(41, 137, 216, 1) 71%, rgba(137, 180, 255, 1) 95%);
    background-image:
    linear-gradient(125deg, rgba(206, 190, 41, .3) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .3) 71%, rgba(137, 180, 255, .3) 95%),
    linear-gradient(-88deg, rgba(206, 190, 41, .8) 0%, rgba(155, 31, 80, .3) 33%, rgba(41, 137, 216, .7) 71%, rgba(137, 180, 255, .7) 95%),
    linear-gradient(23deg, rgba(206, 190, 41, 1) 0%, rgba(155, 31, 80, .6) 33%, rgba(41, 137, 216, 1) 71%, rgba(137, 180, 255, 1) 95%);

    width:800px;
}

@media (max-width: 800px) {
    .imageForm {
        width:100%;
    }
}
.imageContainer {
    display: flex;
    justify-content: center;
    margin: 20px 0;
}
@media (max-width:1024px) {
    .imageContainer {
        display: block;
        position: absolute;
        left:0;
        right:0;
        margin:0;
    }
}
.currentImage {
    width:auto;
    height:500px;
    opacity:0;
}
.visible {
    opacity:1;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
}

@media (max-width: 600px) {
    .currentImage {
        width:100%;
        height:auto;
        margin:0;
        padding:0;
    }
}
ul.info {
    margin:0;
    background: rgba(255, 255, 255, 0.2);
    height:0;
    -webkit-animation:height 1s forwards;
            animation:height 1s forwards;
    overflow: hidden;
    padding:0;
}
@-webkit-keyframes height {
    100% {height:100%}
}
@keyframes height {
    100% {height:100%}
}
ul.info:hover {
    background: rgba(255, 255, 255, 0.603);
}
li.detected-paragraph {
    display: flex;
    justify-content: space-between;
    padding:3px 10px;
    margin:0;
    border-bottom:1px dotted rgb(25, 63, 8);
}
li.detected-paragraph:last-child {
    border-bottom:none;
}
.detectedName {
    color:rgb(3, 4, 109);
    padding-right:15px;
}
.detectedValue {
    padding-left:2px;
}

@media (max-width:1024px) {
    ul.info {
        position: absolute;
        right:0;
        top:0; 
    }
    }
    @media (max-width:600px) {
        ul.info {
            margin:0;
            position:static;
            width:100%;
            height:auto;
            background: rgba(255, 255, 255, 0.603);
        }
    }
.main {
    padding:40px;
    border:1px solid rgba(100,100,100,0.6);
    border-radius:8px;
}
